/// <reference path="../global.d.ts" />

import { stub } from 'sinon';
import { DataSetMock } from '../ComponentFramework-Mock';
export const mockRefreshDatasets = (mockGenerator, callback) => {
  mockGenerator.RefreshDatasets = stub();
  mockGenerator.RefreshDatasets.callsFake(() => {
    Object.getOwnPropertyNames(mockGenerator.context._parameters).forEach(propertyName => {
      const mock = mockGenerator.context._parameters[propertyName];
      if (!(mock instanceof DataSetMock) || !mock._loading || mock._timeoutRef !== undefined) {
        return;
      }
      mock._timeoutRef = setTimeout(() => {
        mock._loading = !mock._loading;
        mockGenerator.RefreshParameters();
        mockGenerator.context.updatedProperties = [propertyName, 'dataset'];
        callback();
        mock._onLoaded();
        mock._timeoutRef = undefined;
      }, mock._delay);
    });
  });
};