import { stub } from 'sinon';
import { AttributeType } from '../ComponentFramework-Mock';
export class AttributeMetadataGenerator {
  constructor(entityLogicalName) {
    this.Attributes = void 0;
    this.AddString = void 0;
    this.AddBoolean = void 0;
    this.AddInteger = void 0;
    this.Attributes = [];
    this.AddString = stub();
    this.AddString.callsFake(args => {
      this.Attributes = this.Attributes.concat(args.map(logicalName => ({
        EntityLogicalName: entityLogicalName,
        LogicalName: logicalName,
        AttributeType: AttributeType.String
      })));
      return this;
    });
    this.AddBoolean = stub();
    this.AddBoolean.callsFake(args => {
      this.Attributes = this.Attributes.concat(args.map(logicalName => ({
        EntityLogicalName: entityLogicalName,
        LogicalName: logicalName,
        AttributeType: AttributeType.Boolean
      })));
      return this;
    });
    this.AddInteger = stub();
    this.AddInteger.callsFake(args => {
      this.Attributes = this.Attributes.concat(args.map(logicalName => ({
        EntityLogicalName: entityLogicalName,
        LogicalName: logicalName,
        AttributeType: AttributeType.Integer
      })));
      return this;
    });
  }
}