    this.lastPageNumber = void 0;
    this.pageSize = void 0;
    this.hasNextPage = void 0;
    this.hasPreviousPage = void 0;
    this.loadNextPage = void 0;
    this.loadPreviousPage = void 0;
    this.reset = void 0;
    this.setPageSize = void 0;
