import { stub } from 'sinon';
export class EntityRecordMock {
  constructor(db, etn, id) {
    this._db = void 0;
    this._boundRow = void 0;
    this._boundTable = void 0;
    this.getFormattedValue = void 0;
    this.getRecordId = void 0;
    this.getValue = void 0;
    this.getNamedReference = void 0;
    this._db = db;
    this._boundTable = etn;
    this._boundRow = id;
    this.getFormattedValue = stub();
    this.getFormattedValue.callsFake(columnName => {
      const {
        value,
        attributeMetadata
      } = this._db.GetValueAndMetadata(this._boundTable, columnName, this._boundRow);
      return value === undefined ? value : '' + value;
    });
    this.getNamedReference = stub();
    this.getNamedReference.callsFake(() => {
      const {
        row,
        entityMetadata
      } = this._db.GetRow(this._boundTable, this._boundRow);
      if (!row) {
        return {
          id: {
            guid: ''
          },
          name: ''
        };
      }
      const id = {
        guid: row?.[entityMetadata ? entityMetadata.PrimaryIdAttribute || entityMetadata.LogicalName + 'id' : 'id']
      };
      const etn = entityMetadata?.LogicalName;
      const name = row?.[entityMetadata?.PrimaryNameAttribute || 'name'];
      return {
        id,
        etn,
        name
      };
    });
    this.getRecordId = stub();
    this.getRecordId.callsFake(() => id);
    this.getValue = stub();
    this.getValue.callsFake(columnName => {
      const {
        value,
        attributeMetadata
      } = this._db.GetValueAndMetadata(this._boundTable, columnName, this._boundRow);
      return value;
    });
  }
}