import { stub } from 'sinon';
import { EntityRecordMock } from './DataSetApi/EntityRecord.mock';
import { FilteringMock } from './DataSetApi/Filtering.mock';
import { LinkingMock } from './DataSetApi/Linking.mock';
import { PagingMock } from './DataSetApi/Paging.mock';
import { AttributeMetadataGenerator } from '../../utils/AttributeMetadataGenerator';
export class DataSetMock {
  constructor(propertyName, db) {
    this._boundColumn = void 0;
    this._boundTable = void 0;
    this._boundRow = void 0;
    this._db = void 0;
    this._Bind = void 0;
    this._Refresh = void 0;
    this._InitItems = void 0;
    this._loading = void 0;
    this._timeoutRef = void 0;
    this._onLoaded = void 0;
    this._delay = void 0;
    this._SelectedRecordIds = void 0;
    this.addColumn = void 0;
    this.columns = void 0;
    this.error = void 0;
    this.errorMessage = void 0;
    this.filtering = void 0;
    this.linking = void 0;
    this.loading = void 0;
    this.paging = void 0;
    this.records = void 0;
    this.sortedRecordIds = void 0;
    this.sorting = void 0;
    this.clearSelectedRecordIds = void 0;
    this.getSelectedRecordIds = void 0;
    this.getTargetEntityType = void 0;
    this.getTitle = void 0;
    this.getViewId = void 0;
    this.openDatasetItem = void 0;
    this.refresh = void 0;
    this.setSelectedRecordIds = void 0;
    this._boundTable = `!!${propertyName}`;
    this._boundColumn = propertyName;
    this._db = db;
    this._SelectedRecordIds = [];
    this._onLoaded = stub();
    this.error = false;
    this.errorMessage = '';
    this.linking = new LinkingMock();
    const dataSetEntity = {
      LogicalName: '!!' + propertyName,
      EntitySetName: '!!' + propertyName,
      PrimaryIdAttribute: 'id',
      PrimaryNameAttribute: 'name',
      Attributes: []
    };
    const dataSetColumnsEntity = {
      LogicalName: '!!' + propertyName + '@columns',
      EntitySetName: '!!' + propertyName + '@columns',
      PrimaryIdAttribute: 'name',
      PrimaryNameAttribute: 'displayName',
      Attributes: []
    };
    this._db.initMetadata([dataSetEntity, dataSetColumnsEntity]);
    this._Refresh = stub();
    this._Bind = stub();
    this._Bind.callsFake((boundTable, boundColumn, boundRow) => {
      this._boundColumn = boundColumn;
      this._boundRow = boundRow;
      this._boundTable = boundTable;
    });
    this._InitItems = stub();
    this._InitItems.callsFake(items => {
      const tableMetadata = this._db.getTableMetadata(`${this._boundTable}`);
      if (tableMetadata) {
        items.forEach((item, i) => {
          if (item[tableMetadata?.PrimaryIdAttribute || 'id'] === undefined) {
            item[tableMetadata?.PrimaryIdAttribute || 'id'] = i + '';
          }
        });
      }
      let i = 0;
      let columns = {};
      items.forEach(item => {
        Object.getOwnPropertyNames(item).forEach(key => {
          if (!(key in columns)) {
            columns[key] = i++;
          }
        });
      });
      new AttributeMetadataGenerator(this._boundTable).AddString(Object.getOwnPropertyNames(columns)).Attributes.forEach(attribute => {
        this._db.upsertAttributeMetadata(this._boundTable, attribute);
      });
      new AttributeMetadataGenerator(`${this._boundTable}@columns`).AddString(['displayName', 'name', 'dataType', 'alias']).AddInteger(['order', 'visualSizeFactor']).Attributes.forEach(attribute => {
        this._db.upsertAttributeMetadata(`${this._boundTable}@columns`, attribute);
      });
      this._db.initItems({
        '@odata.context': `#${this._boundTable}@columns`,
        value: Object.getOwnPropertyNames(columns).map(column => {
          return {
            displayName: column,
            name: column,
            dataType: 'SingleLine.Text',
            alias: column,
            order: columns[column],
            visualSizeFactor: 1
          };
        })
      });
      this._db.initItems({
        '@odata.context': `#${this._boundTable}`,
        value: items
      });
      this._Refresh();
    });
    this._Refresh.callsFake(() => {
      const columnsResult = this._db.GetAllRows(`${this._boundTable}@columns`);
      this.columns = columnsResult.rows;
      const rows = this._db.GetAllRows(this._boundTable);
      const records = this._loading ? [] : rows.rows.map(item => {
        const row = new EntityRecordMock(db, this._boundTable, item[rows.entityMetadata?.PrimaryIdAttribute || 'id']);
        return row;
      });
      this.records = {};
      this.loading = this._loading;
      records.forEach(record => {
        this.records[record.getRecordId()] = record;
      });
      this.paging.pageSize = this.paging._pageSize;
      this.paging.totalResultCount = records.length;
      if (this.sorting.length > 0) {
        const sort = this.sorting[0];
        this.sortedRecordIds = records.sort((record1, record2) => sort.sortDirection === 0 ? record1.getFormattedValue(sort.name)?.localeCompare(record2.getFormattedValue(sort.name)) : record2.getFormattedValue(sort.name)?.localeCompare(record1.getFormattedValue(sort.name))).map(record => record.getRecordId());
      } else {
        this.sortedRecordIds = records.sort((record1, record2) => record1.getNamedReference()?.name?.localeCompare(record2.getNamedReference()?.name)).map(record => record.getRecordId());
      }
    });
    this.loading = true;
    this._loading = true;
    this._delay = 200;
    this.sortedRecordIds = [];
    this.sorting = [];
    this.columns = [];
    this.paging = new PagingMock();
    this.filtering = new FilteringMock();
    this.records = {};
    this.clearSelectedRecordIds = stub();
    this.clearSelectedRecordIds.callsFake(() => {
      this._SelectedRecordIds = [];
    });
    this.getSelectedRecordIds = stub();
    this.getSelectedRecordIds.callsFake(() => [...this._SelectedRecordIds]);
    this.addColumn = stub();
    this.getTargetEntityType = stub();
    this.getTargetEntityType.callsFake(() => {
      return this._boundTable;
    });
    this.getTitle = stub();
    this.getViewId = stub();
    this.openDatasetItem = stub();
    this.refresh = stub();
    this.setSelectedRecordIds = stub();
    this.setSelectedRecordIds.callsFake(ids => {
      if (!ids) {
        this._SelectedRecordIds = [];
        return;
      }
      // validate
      for (let i = 0; i < ids.length; i++) {
        const result = this._db.GetRow(this._boundTable, ids[i]);
        if (!result.row) {
          throw new Error(`Record with id '${ids[i]}' does not exist.`);
        }
      }
      this._SelectedRecordIds = ids;
    });
  }
}