import { stub } from 'sinon';
export class PropertyMock {
  /**
   * Used by the framework to refresh the property value using the in-memory database.
   */

  /** 
   * Shortcut to update the value in the database using the binding information of the property.
   */

  constructor(db, boundTable, boundColumn) {
    this._boundColumn = void 0;
    this._boundRow = void 0;
    this._boundTable = void 0;
    this._db = void 0;
    this._Refresh = void 0;
    this._Bind = void 0;
    this._SetValue = void 0;
    this.error = void 0;
    this.errorMessage = void 0;
    this.formatted = void 0;
    this.security = void 0;
    this.raw = void 0;
    this.type = void 0;
    this.attributes = void 0;
    this._db = db;
    this._boundTable = boundTable;
    this._boundColumn = boundColumn;
    this._boundRow = undefined;
    this._Refresh = stub();
    this._SetValue = stub();
    this._Bind = stub();
    this._Bind.callsFake((boundTable, boundColumn, boundRow) => {
      this._boundColumn = boundColumn;
      this._boundRow = boundRow;
      this._boundTable = boundTable;
    });
    this.raw = null;
    this.error = false;
    this.errorMessage = '';
    this.type = '';
  }
}