import { stub } from 'sinon';
import { parseOData } from '@shko.online/dataverse-odata';
import { newGuid } from '../utils/newGuid';
export class WebApiMock {
  constructor(db) {
    this._Delay = void 0;
    this.createRecord = void 0;
    this.deleteRecord = void 0;
    this.updateRecord = void 0;
    this.retrieveMultipleRecords = void 0;
    this.retrieveRecord = void 0;
    this._Delay = 200;
    this.createRecord = stub();
    this.createRecord.callsFake((entityType, data) => {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          const metadata = db.getTableMetadata(entityType);
          if (!metadata) {
            return reject({
              message: `Entity ${entityType} does not exist.`
            });
          }
          const newObject = {
            id: newGuid(),
            name: data[metadata.PrimaryNameAttribute || 'name'],
            entityType: entityType
          };
          data[metadata.PrimaryIdAttribute || entityType + 'id'] = newObject.id;
          db.AddRow(entityType, data, metadata);
          resolve(newObject);
        }, this._Delay);
      });
    });
    this.deleteRecord = stub();
    this.deleteRecord.callsFake((entityType, id) => {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          const result = db.GetRow(entityType, id);
          if (!result.entityMetadata) {
            return reject({
              message: `Entity ${entityType} does not exist.`
            });
          }
          if (!result.row) {
            return reject({
              message: `Could not find record with id: '${id}' for entity: '${entityType}'.`
            });
          }
          db.RemoveRow(entityType, id);
          resolve({
            id,
            name: result.row?.[result.entityMetadata.PrimaryNameAttribute || 'name'],
            entityType
          });
        }, this._Delay);
      });
    });
    this.updateRecord = stub();
    this.updateRecord.callsFake((entityType, id, data) => {
      return new Promise(resolve => {
        resolve({
          id,
          name: 'Any',
          entityType
        });
      });
    });
    this.retrieveMultipleRecords = stub();
    this.retrieveMultipleRecords.callsFake((entityType, options, maxPageSize) => {
      return new Promise(resolve => {
        resolve({
          entities: [],
          nextLink: 'string'
        });
      });
    });
    this.retrieveRecord = stub();
    this.retrieveRecord.callsFake((entityType, id, options) => {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          const result = db.GetRowForAPI(entityType, id);
          if (!result.entityMetadata) {
            return reject({
              message: `Entity ${entityType} does not exist.`
            });
          }
          if (!result.row) {
            return reject({
              message: `Could not find record with id: '${id}' for entity: '${entityType}'.`
            });
          }
          if (options) {
            var parsed = parseOData(options);
            if (parsed.$select) {
              const oldRow = result.row;
              result.row = {};
              parsed.$select.forEach(attribute => {
                result.row[attribute] = oldRow[attribute];
              });
            }
          }
          resolve(result.row);
        }, this._Delay);
      });
    });
  }
}