import { stub } from 'sinon';
export class FilteringMock {
  constructor() {
    this._Filter = void 0;
    this.getFilter = void 0;
    this.setFilter = void 0;
    this.clearFilter = void 0;
    const defaultFilter = {
      conditions: [],
      filterOperator: 0
    };
    this._Filter = defaultFilter;
    this.clearFilter = stub();
    this.clearFilter.callsFake(() => {
      this._Filter = defaultFilter;
    });
    this.getFilter = stub();
    this.getFilter.callsFake(() => Object.assign({}, this._Filter));
    this.setFilter = stub();
    this.setFilter.callsFake(filter => {
      this._Filter = Object.assign({}, filter);
    });
  }
}