import { stub } from 'sinon';
import { AttributeType } from './AttributeType';
export class EnumPropertyMock {
  constructor(propertyName, db, entityMetadata) {
    this._boundColumn = void 0;
    this._boundTable = void 0;
    this._boundRow = void 0;
    this._db = void 0;
    this._Bind = void 0;
    this._Refresh = void 0;
    this._SetValue = void 0;
    this.raw = void 0;
    this.type = void 0;
    this.raw = '';
    this._db = db;
    this._boundColumn = propertyName;
    this._boundTable = entityMetadata.LogicalName;
    this._Refresh = stub();
    this._Bind = stub();
    this._Bind.callsFake((boundTable, boundColumn, boundRow) => {
      this._boundTable = boundTable;
      this._boundColumn = boundColumn;
      this._boundRow = boundRow;
    });
    this._Bind(entityMetadata.LogicalName, propertyName);
    this._SetValue = stub();
    this._SetValue.callsFake(value => {
      this._db.UpdateValue(value, this._boundTable, this._boundColumn, this._boundRow);
    });
    this._Refresh.callsFake(() => {
      const {
        value,
        attributeMetadata
      } = this._db.GetValueAndMetadata(this._boundTable, this._boundColumn, this._boundRow);
      if (attributeMetadata.AttributeType !== AttributeType.Picklist) {
        throw new Error('Type Error');
      }
      this.raw = value;
    });
    const attribute = {
      AttributeType: AttributeType.Picklist,
      EntityLogicalName: entityMetadata.LogicalName,
      LogicalName: propertyName
    };
    entityMetadata.Attributes?.push(attribute);
    this.type = 'EnumProperty';
  }
}